import { Button, DisplayText, Stack } from '@shopify/polaris'
import React, { Component } from 'react'
import ProductsApi from '../../apis/products'

const INITIAL_STATE = {
  isLoadingProducts: false,
  products: null,
  productSelected: null,
  isLoadingProductsCount: false,
  isLoadingProductById: false,
  isCreatingProduct: false,
  isUpdatingProduct: false,
  isDeletingProduct: false,
}

class Products extends Component {
  constructor(props) {
    super(props)
    this.state = JSON.parse(JSON.stringify(INITIAL_STATE))
  }
  getProducts = async () => {
    this.setState({isLoadingProducts: true})
    let res = await ProductsApi.find({})
    console.log('Get Products res :>> ', res)
    if (res.success) {
      let products = res.payload
      let productSelected = products.products[Math.floor(Math.random() * products.products.length)]
      this.setState({isLoadingProducts: false, products, productSelected})
    } else {
      this.setState({isLoadingProducts: false})
    }
  }
  getProductCount = async () => {
    this.setState({isLoadingProductsCount: true})
    let res = await ProductsApi.getCount()
    console.log('Get Products count res :>> ', res)
    this.setState({isLoadingProductsCount: false})
  }
  getProductById = async () => {
    const {productSelected} = this.state
    if (productSelected) {
      console.log('Product selected id: ', productSelected.id);

      this.setState({isLoadingProductById: true})
      let res = await ProductsApi.findById(productSelected.id)
      console.log('Get Products by Id res :>> ', res)
      this.setState({isLoadingProductById: false})
    } else {
      console.log('You have no product id')
      console.log('Get the products list first to get the random id');
    }
  }
  createProduct = async () => {
    this.setState({isCreatingProduct: true})

    let product = {
      title: `Product demo - ${Date.now()}`,
      body_html: '<b>Product demo description</b>',
    }

    let res = await ProductsApi.create(product)
    console.log('Create Product res :>> ', res)
    if (res.success) {
      this.setState({isCreatingProduct: false, productSelected: res.payload.product})
    } else {
      this.setState({isCreatingProduct: false})
    }
  }
  updateProduct = async () => {
    const {productSelected} = this.state
    if (productSelected) {
      console.log('Product selected: ', productSelected);

      this.setState({isUpdatingProduct: true})
      
      let product = {
        id: productSelected.id,
        title: productSelected.title + ' --Updated'
      }
      
      let res = await ProductsApi.update(product)
      console.log('Update Product res :>> ', res)
      if (res.success) {
        this.setState({isUpdatingProduct: false, productSelected: res.payload.product})
      } else {
        this.setState({isUpdatingProduct: false})
      }
    } else {
      console.log('You have no product id')
      console.log('Get the products list first to get the random product');
    }
  }
  deleteProduct = async () => {
    const {productSelected} = this.state
    if (productSelected) {
      console.log('Product selected: ', productSelected);

      this.setState({isDeletingProduct: true})
      let res = await ProductsApi.delete(productSelected.id)
      console.log('Delete Product res :>> ', res)
      if (res.success) {
        this.setState({isDeletingProduct: false, productSelected: null})
      } else {
        this.setState({isDeletingProduct: false})
      }
    } else {
      console.log('You have no product id')
      console.log('Get the products list first to get the random product');
    }
  }
  render() {
    const {
      isLoadingProducts,
      isLoadingProductsCount,
      isLoadingProductById,
      isCreatingProduct,
      isUpdatingProduct,
      isDeletingProduct,
      productSelected,
    } = this.state
    return (
      <Stack vertical>
        <DisplayText size="small">Products Page</DisplayText>

        <Stack>
          <Button onClick={() => this.getProducts()} loading={isLoadingProducts}>Get Products</Button>
          <Button onClick={() => this.getProductCount()} loading={isLoadingProductsCount}>Get Products count</Button>
          <Button onClick={() => this.getProductById()} loading={isLoadingProductById} disabled={!productSelected}>Get Products by Id</Button>
          <Button onClick={() => this.createProduct()} loading={isCreatingProduct}>Create Product</Button>
          <Button onClick={() => this.updateProduct()} loading={isUpdatingProduct} disabled={!productSelected}>Update Product</Button>
          <Button onClick={() => this.deleteProduct()} loading={isDeletingProduct} disabled={!productSelected}>Delete Product</Button>
        </Stack>
        <div>Results displayed in console.log (Press F12)</div>
      </Stack>
    )
  }
}

export default Products
